import React, { useState } from "react";
import "./styles.css";
import { totp } from "otplib";

export default function App() {
  const [token, setToken] = useState(null);

  totp.options = { digits: 6, step: 30 };

  const getToken = () => {

    setToken(null);

    setTimeout(() => {
      const newToken = totp.generate("KVKFKRCPNZQUYMLXOVYDSQKJKZDTSRLD");
      setToken(newToken);
    }, 2000);
    
  };

  return (
    <div className="App">
      <button type="button" onClick={getToken}>
        Generate
      </button>
      {
        token && (
          <div>
            <h1>{token}</h1>
          </div>
        )
      }
    </div>
  );
}
